
    import {defineComponent} from "vue";
    import CnRoutes from "@/components/commons/CnRoutes.vue";

    export default defineComponent({
        components:{CnRoutes},
        props:{
            showMenuPhone:{
                type: Boolean,
                required: true
            }
        },
        emits: ['closeMenuPhone'],
        setup(props, context){
            const closeMenuPhone = () => {
                context.emit('closeMenuPhone')
            }

            return{
                closeMenuPhone
            }
        }
    })
