
import {defineComponent, computed} from "vue";

export default defineComponent({
    props: {
        link: {
            type: String,
            required: true
        },
        imgNameExtension: {
            type: String,
            required: true
        },
        company: {
            type: String,
            required: true
        },
        catchPhrase: {
            type: String,
            required: true
        }
    },
    setup(props) {
        const fullPath = computed(() => require(`@/assets/${props.imgNameExtension}`));

        return {
            fullPath
        }
    }
})
