
    import {defineComponent, computed} from "vue";

    export default defineComponent({
        props:{
            paddingLeft: {
                required: false
            }
        },
        setup(props){
            const padding = computed(() => {
                if(props.paddingLeft)
                    return props.paddingLeft
                return 10;
            })

            const paddingStyle = computed(() => {
                return{
                    'padding-left': padding.value + '%'
                }
            })

            return{
                paddingStyle
            }
        }
    })
