
import {defineComponent, computed} from "vue";

export default defineComponent({
    props: {
        clientName: {
            type: String,
            required: false
        },
        clientPosition: {
            type: String,
            required: false
        },
        urlPic: {
            type: String,
            required: false
        },
        quote: {
            type: String,
            required: false
        }
    },
    setup(props) {
        const fullPathPic = computed(() => {
            if (props.urlPic !== null)
                return require(`@/assets/projects/${props.urlPic}`);
            return null;
        })

        const fullNoPics = computed(() => {
            return {
                'align-items': props.urlPic === null ? 'flex-start' : 'center'
            }
        })

        const classFullNoPics = computed(() => {
            return {
                noPics: props.urlPic === null
            }
        })

        return {
            fullPathPic,
            fullNoPics,
            classFullNoPics
        }
    }
})
