
    import {defineComponent, computed} from "vue";
    import CnTitle from "@/components/commons/CnTitle.vue";

    export default defineComponent({
        components:{
            CnTitle
        },
        props:{
            imgName:{
                type: String,
                required: true
            },
            title:{
                type: String,
                required: true
            }
        },
        setup(props){
            const fullPath = computed(() => require(`@/assets/icons/${props.imgName}.svg`));

            return{
                fullPath
            }
        }
    })
