
import {defineComponent, computed} from "vue";

export default defineComponent({
    props: {
        imgLogoExtension: {
            type: String,
            required: true
        },
        title: {
            type: String,
            required: true
        },
        onLeft: {
            type: Boolean,
            required: true
        }
    },
    setup(props) {
        const fullPath = computed(() => require(`@/assets/services/${props.imgLogoExtension}`));

        const cssTitle = computed(() => {
            return {
                titleLeft: props.onLeft,
                titleRight: !props.onLeft
            }
        })

        const cssImg = computed(() => {
            return {
                imgLeft: props.onLeft,
                imgRight: !props.onLeft
            }
        })

        return {
            fullPath,
            cssTitle,
            cssImg
        }
    }
})
